import React from 'react';
import { Outlet, Link, useLocation } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { useWeb3React } from '@web3-react/core';

const Layout = () => {
  const { t } = useTranslation();
  const { account, activate, deactivate } = useWeb3React();
  const location = useLocation();

  const isActive = (path) => {
    return location.pathname === path;
  };

  return (
    <div className="min-h-screen bg-gray-100 dark:bg-gray-900">
      <nav className="bg-white dark:bg-gray-800 shadow-lg">
        <div className="container mx-auto px-4">
          <div className="flex justify-between items-center h-16">
            <div className="flex items-center space-x-8">
              <Link to="/" className="text-xl font-bold text-gray-800 dark:text-white">
                {t('app_name')}
              </Link>
              <div className="hidden md:flex space-x-4">
                <Link
                  to="/"
                  className={`px-3 py-2 rounded-md text-sm font-medium ${
                    isActive('/') ? 'bg-gray-900 text-white' : 'text-gray-700 dark:text-gray-300 hover:bg-gray-700 hover:text-white'
                  }`}
                >
                  {t('dashboard')}
                </Link>
                <Link
                  to="/staking"
                  className={`px-3 py-2 rounded-md text-sm font-medium ${
                    isActive('/staking') ? 'bg-gray-900 text-white' : 'text-gray-700 dark:text-gray-300 hover:bg-gray-700 hover:text-white'
                  }`}
                >
                  {t('staking')}
                </Link>
                <Link
                  to="/referral"
                  className={`px-3 py-2 rounded-md text-sm font-medium ${
                    isActive('/referral') ? 'bg-gray-900 text-white' : 'text-gray-700 dark:text-gray-300 hover:bg-gray-700 hover:text-white'
                  }`}
                >
                  {t('referral')}
                </Link>
                <Link
                  to="/rewards"
                  className={`px-3 py-2 rounded-md text-sm font-medium ${
                    isActive('/rewards') ? 'bg-gray-900 text-white' : 'text-gray-700 dark:text-gray-300 hover:bg-gray-700 hover:text-white'
                  }`}
                >
                  {t('rewards')}
                </Link>
              </div>
            </div>
            <div>
              {account ? (
                <button
                  onClick={deactivate}
                  className="bg-red-600 text-white px-4 py-2 rounded-md text-sm font-medium hover:bg-red-700"
                >
                  {t('disconnect')}
                </button>
              ) : (
                <button
                  onClick={activate}
                  className="bg-blue-600 text-white px-4 py-2 rounded-md text-sm font-medium hover:bg-blue-700"
                >
                  {t('connect')}
                </button>
              )}
            </div>
          </div>
        </div>
      </nav>

      <main className="container mx-auto px-4 py-8">
        <Outlet />
      </main>
    </div>
  );
};

export default Layout; 