import React, { useEffect } from 'react';

const GlassToast = ({ message, type = 'info', duration = 3000, onClose }) => {
  useEffect(() => {
    if (duration > 0) {
      const timer = setTimeout(() => {
        onClose && onClose();
      }, duration);
      return () => clearTimeout(timer);
    }
  }, [duration, onClose]);

  let color = 'bg-white bg-opacity-60 text-gray-900 border-gray-200';
  if (type === 'success') color = 'bg-green-200 bg-opacity-60 text-green-900 border-green-300';
  if (type === 'error') color = 'bg-red-200 bg-opacity-60 text-red-900 border-red-300';
  if (type === 'warning') color = 'bg-yellow-200 bg-opacity-60 text-yellow-900 border-yellow-300';

  return (
    <div
      className={`fixed top-6 left-1/2 transform -translate-x-1/2 z-50 px-6 py-3 rounded-xl shadow-xl border backdrop-blur-md ${color} dark:bg-gray-800 dark:bg-opacity-70 dark:text-white dark:border-gray-600 flex items-center space-x-2 animate-fade-in`}
      style={{ minWidth: 200, maxWidth: 400 }}
    >
      <span className="flex-1">{message}</span>
      <button
        onClick={onClose}
        className="ml-2 text-lg font-bold text-gray-500 hover:text-gray-900 dark:text-gray-300 dark:hover:text-white focus:outline-none"
        aria-label="Close"
      >
        ×
      </button>
    </div>
  );
};

export default GlassToast; 