import React, { createContext, useState, useEffect, useContext } from 'react';
import { ethers } from 'ethers';
import { getContract, getTokenContract, NETWORKS } from '../utils/contract';
import { toast } from 'react-toastify';
import glassToast from '../utils/toastUtils.jsx';

const Web3Context = createContext();

export const useWeb3 = () => useContext(Web3Context);

export const Web3Provider = ({ children }) => {
  const [account, setAccount] = useState(null);
  const [provider, setProvider] = useState(null);
  const [contract, setContract] = useState(null);
  const [tokenContract, setTokenContract] = useState(null);
  const [loading, setLoading] = useState(false);
  const [chainId, setChainId] = useState(null);
  const [isConnected, setIsConnected] = useState(false);
  const [error, setError] = useState('');

  // 连接钱包
  const connectWallet = async () => {
    try {
      setLoading(true);
      if (!window.ethereum) {
        setError('请安装MetaMask钱包!');
        glassToast.error('请安装MetaMask钱包!');
        return;
      }

      const provider = new ethers.providers.Web3Provider(window.ethereum);
      await window.ethereum.request({ method: 'eth_requestAccounts' });
      const signer = provider.getSigner();
      const address = await signer.getAddress();
      const network = await provider.getNetwork();

      // 检查网络
      if (!NETWORKS[network.chainId]) {
        // 尝试切换到 Sepolia 测试网
        try {
          await window.ethereum.request({
            method: 'wallet_switchEthereumChain',
            params: [{ chainId: '0xaa36a7' }], // Sepolia chainId
          });
          // 重新连接
          return connectWallet();
        } catch (switchError) {
          if (switchError.code === 4902) {
            try {
              await window.ethereum.request({
                method: 'wallet_addEthereumChain',
                params: [
                  {
                    chainId: '0xaa36a7',
                    chainName: 'Sepolia',
                    nativeCurrency: {
                      name: 'ETH',
                      symbol: 'ETH',
                      decimals: 18
                    },
                    rpcUrls: ['https://sepolia.infura.io/v3/9aa3d95b3bc440fa88ea12eaa4456161'],
                    blockExplorerUrls: ['https://sepolia.etherscan.io']
                  }
                ]
              });
              // 重新连接
              return connectWallet();
            } catch (addError) {
              throw new Error('无法添加 Sepolia 测试网');
            }
          }
          throw new Error('请切换到 Sepolia 测试网');
        }
      }

      setProvider(provider);
      setAccount(address);
      setChainId(network.chainId);
      setIsConnected(true);

      // 初始化合约
      const contract = getContract(provider);
      const tokenContract = getTokenContract(provider);
      setContract(contract);
      setTokenContract(tokenContract);

      setError('');
      glassToast.success('钱包连接成功!');
    } catch (error) {
      console.error('连接钱包失败', error);
      setError(error.message || '连接钱包失败，请重试!');
      glassToast.error(error.message || '连接钱包失败，请重试!');
    } finally {
      setLoading(false);
    }
  };

  // 断开连接
  const disconnectWallet = () => {
    setAccount(null);
    setProvider(null);
    setContract(null);
    setTokenContract(null);
    setIsConnected(false);
    glassToast.info('钱包已断开连接');
  };

  // 格式化地址显示
  const formatAddress = (address) => {
    if (!address) return '';
    if (address === '0x0000000000000000000000000000000000000000') return '无推荐人';
    return `${address.substring(0, 6)}...${address.substring(address.length - 4)}`;
  };

  // 监听账户变化
  useEffect(() => {
    if (!window.ethereum) return;

    const handleAccountsChanged = (accounts) => {
      if (accounts.length > 0) {
        setAccount(accounts[0]);
      } else {
        setAccount(null);
        setIsConnected(false);
        setContract(null);
        setTokenContract(null);
      }
    };

    const handleChainChanged = (chainId) => {
      const newChainId = parseInt(chainId, 16);
      setChainId(newChainId);
      
      // 检查是否是支持的网络
      if (!NETWORKS[newChainId]) {
        setIsConnected(false);
        setContract(null);
        setTokenContract(null);
        glassToast.error('请切换到 Sepolia 测试网');
      } else {
        // 重新初始化合约
        const provider = new ethers.providers.Web3Provider(window.ethereum);
        const contract = getContract(provider);
        const tokenContract = getTokenContract(provider);
        setProvider(provider);
        setContract(contract);
        setTokenContract(tokenContract);
        setIsConnected(true);
      }
    };

    window.ethereum.on('accountsChanged', handleAccountsChanged);
    window.ethereum.on('chainChanged', handleChainChanged);

    return () => {
      window.ethereum.removeListener('accountsChanged', handleAccountsChanged);
      window.ethereum.removeListener('chainChanged', handleChainChanged);
    };
  }, []);

  // 自动尝试连接
  useEffect(() => {
    if (window.ethereum) {
      // 检查是否已经连接
      window.ethereum.request({ method: 'eth_accounts' })
        .then(accounts => {
          if (accounts.length > 0) {
            connectWallet();
          }
        })
        .catch(console.error);
    }
  }, []);

  const value = {
    account,
    provider,
    contract,
    tokenContract,
    loading,
    chainId,
    isConnected,
    connectWallet,
    disconnectWallet,
    formatAddress,
    error
  };

  return (
    <Web3Context.Provider value={value}>
      {children}
    </Web3Context.Provider>
  );
}; 