import React from 'react';
import { toast } from 'react-toastify';
import GlassToast from '../components/GlassToast';

// 自定义玻璃拟态风格的 toast 工具函数
const glassToast = {
  success: (message, options = {}) => {
    return toast(
      <GlassToast type="success">
        {message}
      </GlassToast>, 
      { 
        className: 'glass-toast-container', 
        closeButton: false, 
        hideProgressBar: true,
        autoClose: 5000,
        pauseOnHover: true,
        draggable: true,
        ...options
      }
    );
  },
  
  error: (message, options = {}) => {
    return toast(
      <GlassToast type="error">
        {message}
      </GlassToast>, 
      { 
        className: 'glass-toast-container', 
        closeButton: false, 
        hideProgressBar: true,
        autoClose: 5000,
        pauseOnHover: true,
        draggable: true,
        ...options
      }
    );
  },
  
  info: (message, options = {}) => {
    return toast(
      <GlassToast type="info">
        {message}
      </GlassToast>, 
      { 
        className: 'glass-toast-container', 
        closeButton: false, 
        hideProgressBar: true,
        autoClose: 5000,
        pauseOnHover: true,
        draggable: true,
        ...options
      }
    );
  },
  
  warning: (message, options = {}) => {
    return toast(
      <GlassToast type="warning">
        {message}
      </GlassToast>, 
      { 
        className: 'glass-toast-container', 
        closeButton: false, 
        hideProgressBar: true,
        autoClose: 5000,
        pauseOnHover: true,
        draggable: true,
        ...options
      }
    );
  }
};

export default glassToast; 