import { ethers } from 'ethers';
import { formatUnits, parseUnits, isAddress } from 'ethers/lib/utils';

// 网络配置（可根据实际需要补充）
export const NETWORKS = {
  mainnet: {
    chainId: 1,
    name: 'Ethereum Mainnet',
    rpcUrl: 'https://mainnet.infura.io/v3/YOUR_INFURA_ID'
  },
  goerli: {
    chainId: 5,
    name: 'Goerli Testnet',
    rpcUrl: 'https://goerli.infura.io/v3/YOUR_INFURA_ID'
  }
};

// 合约地址
export const CONTRACT_ADDRESSES = {
  staking: '0x...', // 替换为实际的质押合约地址
  token: '0x...',   // 替换为实际的代币合约地址
  rewards: '0x...'  // 替换为实际的奖励合约地址
};

// 合约 ABI
export const CONTRACT_ABIS = {
  staking: [
    'function stake(uint256 amount) external',
    'function unstake(uint256 amount) external',
    'function getStakedBalance(address account) external view returns (uint256)',
    'function getRewards(address account) external view returns (uint256)',
    'function claimRewards() external'
  ],
  token: [
    'function balanceOf(address account) external view returns (uint256)',
    'function approve(address spender, uint256 amount) external returns (bool)',
    'function allowance(address owner, address spender) external view returns (uint256)',
    'function transfer(address to, uint256 amount) external returns (bool)'
  ],
  rewards: [
    'function getRewardRate() external view returns (uint256)',
    'function getTotalRewards() external view returns (uint256)',
    'function getClaimedRewards(address account) external view returns (uint256)',
    'function claimRewards() external'
  ]
};

// 获取合约实例
export const getContract = (address, abi, signerOrProvider) => {
  return new ethers.Contract(address, abi, signerOrProvider);
};

// 获取代币合约实例
export const getTokenContract = (address, signerOrProvider) => {
  return new ethers.Contract(address, CONTRACT_ABIS.token, signerOrProvider);
};

// 格式化代币金额
export const formatTokenAmount = (amount, decimals = 18) => {
  return formatUnits(amount, decimals);
};

// 解析代币金额
export const parseTokenAmount = (amount, decimals = 18) => {
  return parseUnits(amount, decimals);
};

// 检查地址是否有效
export const isValidAddress = (address) => {
  return isAddress(address);
};

// 获取合约事件
export const getContractEvents = async (contract, eventName, fromBlock = 0) => {
  const filter = contract.filters[eventName]();
  return await contract.queryFilter(filter, fromBlock);
};

// 等待交易确认
export const waitForTransaction = async (tx) => {
  return await tx.wait();
};

// 获取代币余额
export const getTokenBalance = async (tokenContract, address) => {
  const balance = await tokenContract.balanceOf(address);
  return formatTokenAmount(balance);
};

// 获取质押余额
export const getStakedBalance = async (stakingContract, address) => {
  const balance = await stakingContract.getStakedBalance(address);
  return formatTokenAmount(balance);
};

// 获取奖励余额
export const getRewardsBalance = async (stakingContract, address) => {
  const rewards = await stakingContract.getRewards(address);
  return formatTokenAmount(rewards);
};

// 检查代币授权
export const checkAllowance = async (tokenContract, owner, spender) => {
  const allowance = await tokenContract.allowance(owner, spender);
  return allowance;
};

// 授权代币
export const approveToken = async (tokenContract, spender, amount) => {
  const tx = await tokenContract.approve(spender, amount);
  return await waitForTransaction(tx);
};

// 质押代币
export const stakeTokens = async (stakingContract, amount) => {
  const tx = await stakingContract.stake(amount);
  return await waitForTransaction(tx);
};

// 解除质押
export const unstakeTokens = async (stakingContract, amount) => {
  const tx = await stakingContract.unstake(amount);
  return await waitForTransaction(tx);
};

// 领取奖励
export const claimRewards = async (stakingContract) => {
  const tx = await stakingContract.claimRewards();
  return await waitForTransaction(tx);
}; 