import React from 'react';
import { useTranslation } from 'react-i18next';
import { useWeb3React } from '@web3-react/core';

const Dashboard = () => {
  const { t } = useTranslation();
  const { account, library } = useWeb3React();

  return (
    <div className="container mx-auto px-4 py-8">
      <h1 className="text-3xl font-bold mb-8">{t('dashboard')}</h1>
      {!account ? (
        <div className="text-center py-8">
          <p className="text-lg text-gray-600">{t('please_connect_wallet')}</p>
        </div>
      ) : (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
          <div className="bg-white dark:bg-gray-800 rounded-lg shadow-lg p-6">
            <h2 className="text-xl font-semibold mb-4">{t('total_supply')}</h2>
            <p className="text-3xl font-bold text-blue-600">0.00</p>
          </div>
          <div className="bg-white dark:bg-gray-800 rounded-lg shadow-lg p-6">
            <h2 className="text-xl font-semibold mb-4">{t('total_borrowed')}</h2>
            <p className="text-3xl font-bold text-red-600">0.00</p>
          </div>
          <div className="bg-white dark:bg-gray-800 rounded-lg shadow-lg p-6">
            <h2 className="text-xl font-semibold mb-4">{t('utilization_rate')}</h2>
            <p className="text-3xl font-bold text-green-600">0%</p>
          </div>
          <div className="bg-white dark:bg-gray-800 rounded-lg shadow-lg p-6">
            <h2 className="text-xl font-semibold mb-4">{t('your_stake')}</h2>
            <p className="text-3xl font-bold text-purple-600">0.00</p>
          </div>
          <div className="bg-white dark:bg-gray-800 rounded-lg shadow-lg p-6">
            <h2 className="text-xl font-semibold mb-4">{t('available_rewards')}</h2>
            <p className="text-3xl font-bold text-yellow-600">0.00</p>
          </div>
          <div className="bg-white dark:bg-gray-800 rounded-lg shadow-lg p-6">
            <h2 className="text-xl font-semibold mb-4">{t('health_factor')}</h2>
            <p className="text-3xl font-bold text-indigo-600">∞</p>
          </div>
        </div>
      )}
    </div>
  );
};

export default Dashboard; 